import React, { forwardRef } from "react";

import { Icon } from "../Icon/Icon";
import { colors } from "../styleguide";

import { IconWrapper, StyledIconButton } from "./MenuBarIconButton.styles";
import { MenuBarIconButtonProps } from "./MenuBarIconButton.types";

const MenuBarIconButton = forwardRef<HTMLButtonElement, MenuBarIconButtonProps>(({
	dataQA,
	icon,
	className,
	ariaLabel,
	...props
}, ref) => (
	<StyledIconButton
		ref={ref}
		data-qa={dataQA}
		className={className}
		aria-label={ariaLabel}
		{...props}
	>
		<IconWrapper data-qa={`${dataQA}-icon`}>
			<Icon name={icon} size={16} stroke={`${colors.black}A8`} />
		</IconWrapper>
	</StyledIconButton>
));
MenuBarIconButton.displayName = "MenuBarIconButton";
export default MenuBarIconButton;
